@import '~antd/dist/antd.css';
*{
    padding: 0;
    margin: 0;
}
body{
    background-color: #ccc;
}
.header{
    height: 50px;
    background-color: #444;
}

.header header{
    width: 1200px;
    line-height: 50px;
    margin: 0 auto;
}
.header header a{
    margin: 0 20px;
    color: #fff;
}
.header img{
    width: 120px;
}
.header input{
    text-indent: 10px;
    height: 1.8rem;
    line-height: 1.9rem;
    width: 150px;;
    border-radius: 30px;
    outline: none;
}
#q{
    margin-top: 15px;
    margin-left: 10px;
    display: inline;
}
/**
  *图标样式
  *绝对定位，自定义颜色
*/
.box i{
    position: absolute;
    top: 4.8rem;
    left: 50%;
    margin-left: -15%;
    color: #B2B2B2;
}
/* content */
.content{
    width: 1200px;
    margin: 30px auto 0;
}
.panel {
    width: 900px;
    float: left;
    margin-bottom: 13px;
    background-color:#fff;
}
.panel .header {
    padding: 10px;
    background: #f6f6f6;
    border-radius: 3px 3px 0 0;
}
/* .topic-tab:active {
    background-color: #80bd01;
    color: #fff;
    padding: 3px 4px;
    border-radius: 3px;
} */
.topic-tab {
    margin: 0 10px;
    color: #80bd01;
}

.panel .cell:nth-child(1) {
    border-top: none;
}
.topic_title {
    margin-right:10px; ;
    max-width: 70%;
    white-space: nowrap;
    display: inline-block;
    vertical-align: middle;
    font-size: 16px;
    line-height: 30px;
}
.panel .cell {
    padding-right: 10px;
    background: #fff;
    border-top: 1px solid #f0f0f0;
}

.cell {
    position: relative;
    padding: 10px 0 10px 10px;
    font-size: 14px;
}
.cell .count_of_replies {
    color: #9e78c0;
}
.count_seperator {
    margin: 0 1px;
    font-size: 10px;
}
.cell .count_of_visits {
    font-size: 10px;
    color: #b4b4b4;
}
.cell .reply_count {
    width: 70px;
    display: inline-block;
    text-align: center;
}
.user_avatar img, .user_big_avatar img {
    width: 30px;
    height: 30px;
    border-radius: 3px;
}

img {
    width: 30px;
    height: auto;
    max-width: 100%;
    vertical-align: middle;
    border: 0;
    -ms-interpolation-mode: bicubic;
}
a.last_time {
    text-decoration: none;
}


.pull-right {
    float: right;
}

.last_time {
    font-size: 11px;
    display: inline-block;
    margin-left: 20px;
    color: #777;
}

.pull-right {
    float: right;
}
#content .changes, .cell, .unstyled li div, .user_name, a.dark, a.topic_title {
    overflow: hidden;
}
.pagination {
    height: 40px;
    top:1000px;
}

.pagination ul>li {
    display: inline;
    
}

ol, ul {
    padding: 0;
    margin: 0 0 10px 25px;
    margin-left: 10px;
}


li {
    width: 50px;
    height: 50px;
    margin-right: -1px;
    border: 1px solid rgb(146, 148, 150);
    display: list-item;
    text-align: -webkit-match-parent;
}



.panel .inner, .panel .inner li {
    line-height: 2em;
}
.right{
    width: 270px;
    float: right;
    height: 500px;
    background-color: white ;
}
.rightp{
    margin-left: 10px;
}
.git{
    color: white;
    width: 100px;
    height: 30px;
    background: rgb(67, 93, 206);
    border-radius:3px ;
    margin-bottom: 20px;
    margin-left: 10px;
}
.tt{
    width: 270px;
    height: 15px;
    background: #ccc;
}
.rightimg{
    margin-top: 10px;
    margin-left: 10px;
    margin-bottom: 10px;
    width: 250px;
    height: 65px;
}
.sidebar{
    width: 270px;
    float: right;
}
/* 登录页面 */
.box{
    height: 200px;
}
.one{
    margin-left: 200px;
    margin-top: 120px;
    font-size: 20px;
}
.name{
    margin-left:10px ;
    border:1px solid rgb(163, 161, 161);
}
.two{
    margin-left: 200px;
    margin-top: 15px;
    font-size: 20px;
}
.pass{
    margin-left:30px ;
    border:1px solid rgb(163, 161, 161);
}
#submit{
    width: 150px;
    height: 40px;
    background: rgb(73, 112, 219);
    margin-left: 270px;
    margin-top: 15px;
    border:1px solid white;
    color: white;
}